<template>
  <div>
    <v-row>
      <v-col md="12" cols="12">
        <BlockItem>
          <span slot="title">دسته بندی پروژه ها</span>
          <template slot="icon">mdi-format-list-bulleted-type</template>

          <v-form ref="form" >
            <v-row v-if="renderComponent" >
              <v-col
                  cols="12"
                  md="3"
              >
                <v-text-field
                    outlined
                    :rules="[$store.state.rules.required(title,'عنوان')]"
                    v-model="title"
                    label="عنوان"
                    dense
                    color="txtf"
                >

                </v-text-field>
              </v-col>
              <v-col
                  cols="12"
                  md="3"
              >
                <v-text-field
                    outlined
                    :rules="[$store.state.rules.required(desc,'توضیحات')]"
                    v-model="desc"
                    label="توضیحات"
                    color="txtf"
                    dense
                >

                </v-text-field>
              </v-col>
              <v-col
                  cols="12"
                  md="2"
              >
                <v-btn v-if="!id && $store.state.permissions && $store.state.permissions['create-product-category']" dark class="dash-btns dash-btns--blue" :disabled="loading" @click="addCategory()">
                  + افزودن
                </v-btn>
<!--                <v-btn v-else dark color="orange darken-2" :disabled="loading" @click="editCategory()">
                  ویرایش
                </v-btn>-->
                <v-btn text dark v-if="id" @click="editCategory()" color="#17a2b8" >
                  <v-icon  color="#17a2b8" :disabled="loading"  >mdi-check</v-icon>
                  ویرایش
                </v-btn>
                <v-btn small text dark v-if="id" @click="cleanEdit()" color="red" >
                <v-icon  color="error" :disabled="loading"  >mdi-close</v-icon>
                </v-btn>
              </v-col>
            </v-row>
          </v-form>

          <v-data-table
              hide-default-footer
              :headers="headers" :loading="loading" :items="data" item-key="id" disable-sort
          >

            <template v-slot:[`item.func`]="{item}">
              <div class="d-flex justify-center">
                <a-sbtn v-if="$store.state.permissions && $store.state.permissions['change-product-category']" @clickBtn="toEdit(item)" color="#17a2b8" icon="mdi-pencil" alt="ویرایش کردن" ></a-sbtn>
                <a-sbtn  v-if="$store.state.permissions && $store.state.permissions['delete-product-category']" @clickBtn="toDelete(item.id,'/projects/api/v1/state/delete-category/')" color="#DC3545" icon="mdi-delete" alt="حذف کردن" ></a-sbtn>
              </div>
            </template>


          </v-data-table>
        </BlockItem>
      </v-col>
    </v-row>
    <DeleteDialog :ddata="deleteData" @deleteFn="fetchCategories()"></DeleteDialog>
  </div>
</template>

<script>
import axios from "axios";
import BlockItem from "@/components/Misc/BlockItem";
import DeleteDialog from "@/components/Misc/DeleteDialog";
import {forceRerender} from "@/components/Misc/forceRerender";
import ASbtn from "@/components/Dashboard/Misc/Components/a-sbtn";

export default {
  name: "DashBlogCategory",
  mixins : [forceRerender],
  components: {
    ASbtn,
    DeleteDialog,
    BlockItem
  },
  created() {
    this.fetchCategories();
  },
  data() {
    return {
      id: null,
      desc: null,
      title: null,
      options: {},
      loading: false,
      data: [],
      deleteData: {
        api: '',
        dialog: false,
        loading: false,
        idSelected: null,
      },
    }
  },
  computed: {
    headers() {
      return [
        {
          text: 'شماره',
          align: 'start',
          class: 'header-table-gray',
          sortable: false,
          value: 'id',
        },
        {
          text: 'عنوان',
          align: 'start',
          class: 'header-table-gray',
          sortable: false,
          value: 'title',
        },
        {
          text: 'توضیحات', class: 'header-table-gray',
          align: 'center',
          value: 'desc',
        },
        {
          text: 'پروژه ها', class: 'header-table-gray',
          align: 'center',
          value: 'category_product_count',
        },
        {
          text: 'عملیات', class: 'header-table-gray',
          align: 'center',
          value: 'func',
        },


      ]
    },
  },
  methods: {
    addCategory() {

      this.$refs.form.validate();
      if (this.$refs.form.validate()) {
        const data = {
          desc : this.desc,
          title : this.title
        }
        this.id= null
        this.desc = null
        this.title = null
        this.loading = true;
        this.forceRerender();
        axios.post(this.$store.state.host + '/projects/api/v1/category/create-category', data, {

          headers: {
            Accept: "application/json",
            Authorization: `Bearer ${this.$store.state.userId}`,
          },

        }).then(() => {
          this.notify('دسته بندی افزوده شد', 'info');
          this.fetchCategories();
        }).catch(error => {
          this.errorHandle(error.response)
        })
      }
    },
    editCategory() {

      this.$refs.form.validate();
      if (this.$refs.form.validate()) {
        const data = {
          id : this.id,
          desc : this.desc,
          title : this.title
        }
        this.id= null
        this.desc = null
        this.title = null
        this.loading = true;
        this.forceRerender();
        axios.patch(this.$store.state.host + '/projects/api/v1/state/change-category/'+data.id, data, {

          headers: {
            Accept: "application/json",
            Authorization: `Bearer ${this.$store.state.userId}`,
          },

        }).then(() => {
          this.notify('دسته بندی ویرایش شد', 'warning');
          this.fetchCategories();
        }).catch(error => {
          this.errorHandle(error.response)
        })
      }
    },
    toEdit(item){
      this.id = item.id;
      this.desc = item.desc;
      this.title = item.title;
    },
    cleanEdit(){
      this.id = null;
      this.title = null;
      this.parent = null;
      this.enabled = true;
      this.name = null;
      this.title = null;
      this.forceRerender();
    },
    toDelete(id, api) {
      this.deleteData.idSelected = id;
      this.deleteData.api = api;
      this.deleteData.dialog = true;
    },

    fetchCategories() {
      this.loading = true;
      axios.get(this.$store.state.host + '/projects/api/v1/category/categories', {
        params: this.options
      }).then(response => {
        this.data = response.data.data;
        this.loading = false;
      }).catch(error => {
        this.errorHandle(error.response)
      })
    }
  }
}
</script>

