<template>
  <v-dialog
      v-model="data.dialog"
      :persistent="data.loading"
      max-width="290"
  >
    <v-card class="pt-2 pr-3">
      <h3>
        از حذف آیتم مورد نظر مطمئن هستید؟
      </h3>
      <v-card-text>در صورت موافقت حذف می شود</v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn
            color="primary"
            text
            :disabled="data.loading"
            @click="data.dialog = false"
        >
          بستن
        </v-btn>
        <v-btn
            color="white"
            style="background-color: darkred"
            text
            :disabled="data.loading"
            :loading="data.loading"
            @click="deleteItem()"
        >
          حذف کن
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import axios from "axios";

export default {
  name: "DeleteDialog",
  props : ["ddata"],
  data () {
     return {
       data : this.ddata
     }
  },
  methods : {
    deleteItem() {
      this.data.loading = true;
      axios.delete(this.$store.state.host + this.data.api + this.data.idSelected, {
        headers: {
          Accept: "application/json",
          Authorization: `Bearer ${this.$store.state.userId}`,
        },
      }).then(() => {
        this.$store.state.notify.color = 'orange';
        this.$store.state.notify.is = true;
        this.$store.state.notify.text = "آیتم حذف شد";
        this.$emit('deleteFn')
      }).catch(error=>{
        this.errorHandle(error.response)
      }).finally(()=>{
        this.$emit('deleteFn')
        this.data.loading = false;
        this.data.dialog = false;
      })

    }
  }
}
</script>

